import React from 'react'
import { Helmet } from 'react-helmet'

const NotFound = () => {
  return (
    <div>
      <Helmet>
        <title>Page Not Found | Shakhor Smith</title>
      </Helmet>
      <h1>Page not found</h1>
    </div>
  )
}

export default NotFound



